import { GlobalOutlined } from "@ant-design/icons";
import { Button, ButtonProps, Tooltip } from "antd";
import { toRelativeUrl } from "components/utils/utils";
import React from "react";
import { Dashboard } from "types";

type DashboardViewButtonProps = {
  dashboard: Dashboard;
} & ButtonProps;

export default function DashboardViewButton({
  dashboard,
  ...props
}: DashboardViewButtonProps) {
  return (
    <Tooltip title="View">
      <Button
        {...props}
        icon={<GlobalOutlined />}
        type="text"
        href={toRelativeUrl(`${dashboard?.baseUrl}`)}
        target="_blank"
      />
    </Tooltip>
  );
}